<template>
	<!-- 自定义属性中传入的是字符串,所以要加'' -->
	<!-- v-theme:column代表的是传进去的参数,用来改变背景颜色 -->
	<!-- 这里传进去了参数，要在main.js中进行操作 -->
	<!-- a4 -->
	<div id="show-blogs" v-theme:column="'wide'">
		<h1>博客总览</h1>
		<!-- a5 -->
		<input type="text" v-model="search" placeholder="搜索">
		<!-- <div class="single-blog" v-for="blog in blogs"> -->

		<!-- 如果搜索内容存在，显示相应内容，反之显示所有 -->
		<!-- 这里的:key不能为blog,只能是string或number类型,故这里选为blog.id -->
		<div class="single-blog" v-for="blog in filteredBlog" :key="blog.id">
			<!-- a3 -->
			<!-- '|'管道符 后面的to-uppercase让标题字母大写,to-uppercase相当于过滤器,这里的to-uppercase实际是自定义过滤器 -->
			<!-- '|'管道左边是实际要拿到的值,将blog.title这个值通过管道传到main.js中自定义过滤器to-uppercase中 -->
			<!-- 路由跳转中的to属性跳转只能在""内,所有这里要v-bind绑定 -->
			<router-link v-bind:to="'/blog/' + blog.id">
				<h3 v-rainbow>{{blog.title | toUppercase}}</h3>
			</router-link>
				<article>{{blog.content | snippet}}</article>
		</div>
	</div>
</template>

<script>
import axios from 'axios'
export default {
	name: 'show-blogs',
	data(){
		return{
			blogs:[],
			search:""
		}
	},
	created(){
			// this.$http.get('http://jsonplaceholder.typicode.com/posts')
			// 请求本地json,参数为json文件路径,json文件规定放在static文件夹里
			// this.$http.get('./../static/posts.json')
			// 从https://vuedemo-452af.firebaseio.com/posts.json请求返回的是一个对象
			// this.$http.get('https://vuedemo-452af.firebaseio.com/posts.json')
			axios.get('/posts.json')
		.then(function(data){// 当请求成功，会返回一个值，用data来接收
			// console.log(data);
			// this.blogs = data.body.slice(0,10);// 返回十条数据
			// console.log(data.json());
			// return data.json();axios不需要你返回对象
			// console.log(data.data);
			return data.data;
		}).then((data) => {
			// 用来存放数据库返回的每一个对象
			var blogsArray = [];
			for(let key in data){
				// 数据库返回的每一个对象data[key]
				// console.log(data[key]);

				// 给每一个对象(data[key])添加一个id,并把key(数据库生成的唯一标识)赋给它
				data[key].id = key;

				// 再把每一个对象添加进数组blogsArray中
				blogsArray.push(data[key]);
			}
			// console.log(blogsArray);
			this.blogs = blogsArray;
		})
	},
	computed:{
		filteredBlog:function(){// a5
			return this.blogs.filter((blog) => {// 这个时候就可以拿到数组当中的每一条数据
				// 匹配title,如果找到就返回true,并显示相应内容;反之就返回false,显示所有内容
				// match()ES6中的方法 a5
				// 如果search(搜索框)中输入了内容，data中的search中也会有相应的内容
				return blog.title.match(this.search);
				// console.log(match(this.search));
			})
		}
	},
	filters:{
		// "to-uppercase":function(value){// 标题大写
		// 	return value.toUpperCase();
		// },
		toUppercase(value){// 局部作用  这里用了ES6的语法
			return value.toUpperCase();
		},
		snippet(value){
			return value.slice(0,100) + "...";
		}
	},
	directives:{// 把全局自定义指令拿到本地
		'rainbow':{
			bind(el,binding,vnode){
				el.style.color = "#"+Math.random().toString(16).slice(2,8);
			}
		}
	}
}
</script>

<style>
#show-blogs{
	max-width: 800px;
	margin: 0 auto;
}
#show-blogs a{
	color: #444;
	text-decoration: none;
}
.single-blog{
	padding: 20px;
	margin: 20px 0;
	box-sizing: border-box;
	background-color: #eee;
	border: 1px dotted #aaa;
}
input[type="text"]{
	padding: 8px;
	width: 100%;
	box-sizing: border-box;
}

</style>



