<template>
	<div id="single-blog">
		<h1>标题：{{blog.title}}</h1>
		<article>内容：{{blog.content}}</article>
		<p>作者：{{blog.author}}</p>
		<p>分类：</p>
		<ul>
			<!-- :key="category"优化,记录标识 -->
			<li v-for="category in blog.categories" :key="category">{{category}}</li>
		</ul>
		<button @click="delSingleBlog()">删除</button>
		<router-link v-bind:to="'/edit/' + id">编辑</router-link>
	</div>
</template>

<script>
	import axios from 'axios'
	export default{
		name:"single-blog",
		data(){
			return{
				// 这里的id是每条博客的，对应的路由也发生改变
				// 通过路由参数拿到它的id
				id:this.$route.params.id,
				blog:{} // 点击进来单独的一条博客
			}
		},
		created(){
			// this.$http.get('https://vuedemo-452af.firebaseio.com/posts/' + this.id +".json")
			axios.get('/posts/' + this.id +".json")
			.then((data) => {
				// console.log(data);
				// this.blog = data.body;
				// return data.json();// 把从数据库返回的数据进行处理
				this.blog = data.data;// 把上面处理好的数据对象存入当前对象的blog中
			})
		},
		methods:{
			delSingleBlog(){
				axios.delete('/posts/' + this.id +".json").then(response =>{
					// 结果返回后跳转到总览页面
					this.$router.push({path:'/'})
				})
			}
		}
	}
</script>

<style>
	#single-blog{
		max-width: 960px;
		margin: 0 auto;
		padding: 20px;
		background-color: #eee;
		border:1px dotted #aaa;
	}
</style>


