// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
// import VueResource from 'vue-resource'// http模块,不推荐使用,官方已停止更新维护
import VueRouter from 'vue-router'// 引用路由模块
import App from './App'
import Routes from './routes'// 引用写好的路由文件(routes.js)
import axios from 'axios'// 目前流行的http库

// 全局配置
axios.defaults.baseURL = 'https://vuedemo-452af.firebaseio.com'
// 以下是配置请求头的信息
// axios.defaults.headers.common['Authorization'] = 'Token' // 默认每个url都会给它请求头,授权的令牌
// axios.defaults.headers.post['Content-type'] = 'application/urlencode'
// axios.defaults.headers.get['Accepts'] = 'application/json'

Vue.config.productionTip = false

// Vue.use(VueResource)
Vue.use(VueRouter)

// 自定义指令 rainbow a3
// Vue.directive('rainbow',{
// 	// 钩子函数
// 	// el 指定的元素
// 	// binding 可以理解成绑定元素中的某个参数，如blog in blogs
// 	bind(el,binding,vnode){
// 		// 让标题颜色随机生成
// 		// toString(16) 数字以十六进制值显示
// 		// slice(2,8) 从第二个开始截取到第八个结束
// 		el.style.color = "#"+Math.random().toString(16).slice(2,8);
// 	}
// })

// 自定义指令 theme a4
Vue.directive('theme',{
	bind(el,binding,vnode){
		if(binding.value == 'wide'){// 在v-theme中传入的是'wide'时
			el.style.maxWidth = '1260px';// 设置最大宽度为1260px,具体参考ShowBlogs.vue中的a4
		}else if (binding.value == 'narrow') {
			el.style.maxWidth = '560px';
		}

		if(binding.arg == 'column'){// 在绑定的元素(theme中有无参数传入)
			el.style.backgroundColor = '#6677cc';
			el.style.padding = '20px';
		}
	}
})

// 自定义过滤器(全局作用)
// Vue.filter("to-uppercase",function(value){// 用value接收传过来的值(blog.title)
// 	return value.toUpperCase();// 将传过来的值转换成大写字母
// })
// Vue.filter("snippet",function(value){// 用来隐藏详细内容
// 	return value.slice(0,100) + "...";// 显示部分内容,隐藏超出部分,并在后面显示...
// })

// 创建路由
const router = new VueRouter({
	routes:Routes,
	mode:"history"// 除去url中的"#"
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  router:router // 在根容器中引用上面创建好的路由
})



