<template>
  <div id="add-blog">
    <h2>添加博客</h2>
    <form v-if="!submmited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required>

      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>

      <div id="checkboxes">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories">
        <label>Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories">
        <label>React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories">
        <label>Angular4.js</label>
        <input type="checkbox" value="Angular4.js" v-model="blog.categories">
      </div>
      <label>作者：</label>
      <select v-model="blog.author">
        <!-- a2 -->
        <option v-for="author in authors" :key="author">{{author}}</option>
      </select>
      <button v-on:click.prevent="post">添加博客</button>
    </form>

    <div v-if="submmited">
      <h3>你的博客发布成功!</h3>
    </div>

    
    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容：</p>
      <p>{{blog.content}}</p>
      <p>博客分类：</p>
      <ul>
        <!-- a1 -->
        <li v-for="category in blog.categories" :key="category">
          {{category}}
        </li>
      </ul>
      <p>作者：{{blog.author}}</p>
    </div>    


  </div>
</template>

<script>
// 用axios代替vue-resource,因为官方已经不对后者再进行维护了,
import axios from 'axios'
export default {
  name: 'add-blog',
  data () {
    return {
      blog:{
        title:"",
        content:"",
        categories:[],// 存放博客分类,选中几个就有几个 a1
        author:""// 存放作者 当你选中哪个作者就存放哪个作者
      },
      authors:["zs","ls","ww"],// 存放作者 a2
      submmited:false // 是否点击提交
    }
  },
  methods:{
    post(){
      // 保存当前对象
      // var _this = this;
      // this.$http.post("http://jsonplaceholder.typicode.com/posts",{
      // 将数据发送到我们的数据库中,https://vuedemo-bdbc8.firebaseio.com/是谷歌数据库地址,并在这里创建posts.json文件
      // 后面的this.blog是把当前的blog对象发送过去
      // this.$http.post("https://vuedemo-452af.firebaseio.com/posts.json",this.blog)
      // axios是目前非常流行的http请求库，基于promise实现异步
      axios.post("/posts.json",this.blog)
      // '=>'ES6中的语法，箭头函数
      // 箭头函数的好处就是这里拿到的this依然是上一层的this,而不是指向一个不知道的位置
      .then((data) => {
        // console.log(data);
        this.submmited = true;
      });
    }
  }
}
</script>

<style scoped>
#add-blog *{
  box-sizing: border-box;
}
#add-blog{
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}
label{
  display: block;
  margin: 20px 0 10px;
}
input[type="text"],textarea,select{
  display: block;
  width: 100%;
  padding: 8px;
}
textarea{
  height: 200px;
}
#checkboxes label{
  display: inline-block;
  margin-top: 0;
}
#checkboxes input{
  display: inline-block;
  margin-right: 10px;
}
button{
  display: block;
  margin: 20px 0;
  background-color: crimson;
  color: #fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}

#preview{
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}
h3{
  margin-top: 10px;
}
</style>



